<template>
  <div>
    <div class="address">
      <div class="top">
        <div class="user-name">收货人：{{address.name}}</div>
        <div class="phone">{{address.phone}}</div>
      </div>
      <div class="middle">
        收货地址：{{allAddress}}
      </div>
      <i class="mint-cell-arrow-right"></i>
    </div>
    <div class="goods">
      <div class="goods-img" :style="{backgroundImage: 'url(' + goods.img + ')'}">   
      </div>
      <div class="goods-info">
        <div class="goods-n">{{goods.name}}</div>
        <div class="goods-price">价格¥{{goods.price}}</div>
      </div>
    </div>
    <div class="submit-order" @click="postOrder">提交订单</div>
  </div>
</template>

<script>
export default {
  data () {
    return {}
  },
  computed: {
    address () {
      return this.$store.state.address.list.filter((item) => {
        return item.status
      })[0]
    },
    allAddress () {
      let {province, city, district, details} = this.address
      return province + city + district + details
    },
    goods () {
      return this.$route.query
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$store.dispatch('getAddress')
    })
  },
  methods: {
    postOrder () {
      let {address, goods, allAddress} = this
      this.$store.dispatch('postOrder', {
        item_id: goods.item_id,
        name: goods.name,
        price: goods.price,
        user_name: address.name,
        phone: address.phone,
        address: allAddress
      })
    }
  }
}
</script>

<style scoped>
.address {
  cursor: pointer;
  padding: 5px 10px;
  font-size: 16px;
  border: 1px solid #ececec;
  height: 90px;
}
.top {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  margin: 5px 0;
}
.goods-n {

}
.goods-price {
  position: absolute;
  bottom: 5px;
}
.submit-order {
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 40%;
  height: 60px;
  line-height: 60px;
  font-size: 20px;
  text-align: center;
  background: #26a2ff;
  color: white;
}
</style>



