import api from '../../api/api'
import {responseHandle} from '../../utils/utils'

export default {
  state: {
    me: undefined
  },
  mutations: {
    listMe (state, payload) {
      state.me = payload
    }
  },
  actions: {
    getMe ({commit, state}, payload) {
      api.getMe(payload)
      .then(({body}) => {
        responseHandle(body, () => {
          commit('listMe', body)
        })
      })
    }
  }
}



