<template>
  <div class="container">
    <mt-swipe :auto="4000" class="swipe">
      <mt-swipe-item v-for="(item, index) in imgs" :key="index" class="swipe-img" :style="{backgroundImage: 'url(' + item + ')'}">
      </mt-swipe-item>
    </mt-swipe>
    <mt-cell title="商品名称">
      <span class="goods-name">{{goods.name}}</span>
    </mt-cell>
      <mt-cell title="所需积分">
      <span class="goods-name">{{goods.price}}</span>
    </mt-cell>
    </mt-cell>
      <mt-cell title="库存">
      <span class="goods-name">{{price}}</span>
    </mt-cell>
    <mt-field label="商品详情" readonly placeholder="商品详情" type="textarea" rows="6" :value="goods.details"></mt-field>
    <footer>
      <div class="btn btn-chart">加入购物车</div>
      <div class="btn btn-buy" @click="onPopup">立即购买</div>
    </footer>
    <mt-popup
      v-model="popup"
      position="bottom"
      class="my-popup"
    >
      <img class="goods-image" :src="specifications[currentIndex].img[0]">
      <mt-radio
        title="规格"
        v-model="current"
        :options="specificationName">
      </mt-radio>
      <mt-field label="库存" placeholder="请输入数字" type="number" :value="specifications[currentIndex].store"></mt-field>
      <div class="generate-order" @click="createOrder">确定</div>
    </mt-popup>
  </div>
</template>

<script>
export default {
  data () {
    return {
      popup: false,
      current: 0
    }
  },
  computed: {
    goods () {
      let {id} = this.$route.query
      let {list} = this.$store.state.items
      return list.filter((item) => {
        return item.id === id
      })[0]
    },
    currentIndex () {
      if (!this.current) {
        return 0
      }
      return this.specificationName.indexOf(this.current)
    },
    specifications () {
      return JSON.parse(this.goods.specifications)
    },
    specificationName () {
      return this.specifications.map((item, index) => {
        return item.name
      })
    },
    imgs () {
      return this.specifications[0].img
    },
    price () {
      return this.specifications[0].store
    }
  },
  methods: {
    onPopup () {
      this.popup = true
      this.current = this.specificationName[0]
    },
    createOrder () {
      let {currentIndex} = this
      this.$router.push({
        path: '/order',
        query: {
          item_id: this.goods.id,
          name: this.goods.name,
          price: this.specifications[currentIndex].price,
          img: this.specifications[currentIndex].img[0]
        }
      })
    }
  }
}
</script>

<style>
.swipe {
  height: 250px;
}
.my-popup {
  width: 100%;
  height: 60%;
}
.swipe-img {
  display: block;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
.goods-name {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 250px;
}
.goods-image {
  position: relative;
  top: -30px;
  left: 30px;
  width: 80px;
  height: 80px;
}
footer {
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  text-align: center;
}
.btn {
  display: block;
  flex: 1;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  width: 50%;
  border: 1px solid #ececec;
}
.generate-order {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 40px;
  text-align: center;
  background: #26a2ff;
  line-height: 40px;
  font-size: 20px;
  color: white;
}
</style>





