<template>
  <div class="container">
    <div class="address-item" v-for="i in list" :key="i.id">
      <div class="top">
        <div class="name">{{i.name}}</div>
        <div class="phone">{{i.phone}}</div>
      </div>
      <div class="middle">{{i.province + i.city + i.district + i.details}}</div>
      <div class="bottom">
        <mt-button size="small" plain @click="addressOn(i)">{{i.status ? '默认地址' : '设为默认'}}</mt-button>
        <div class="op-panel">
          <mt-button size="small" type="primary">编辑</mt-button>
          <mt-button size="small" plain @click="deleteAddress(i.id)">删除</mt-button>
        </div>
      </div>
    </div>
    <div class="addressAdd">
      <mt-button type="primary" size="large" @click="addAddress">添加新地址</mt-button>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    list () {
      return this.$store.state.address.list
    }
  },
  beforeRouteEnter (to, from, next) {
    document.title = '管理收货地址'
    next(vm => {
      vm.$store.dispatch('getAddress')
    })
  },
  methods: {
    deleteAddress (id) {
      this.$store.dispatch('deleteAddress', {
        ids: [id]
      })
    },
    addressOn (i) {
      if (!i.status) {
        this.$store.dispatch('addressOn', {
          id: i.id
        })
      }
    },
    addAddress () {
      this.$router.push('/addAddress')
    }
  }
}
</script>

<style scoped>
.address-item {
  margin: 10px 0;
  padding: 5px 10px;
  background: #ececec;
}
.top {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
}
.middle {
  margin: 5px 0;
}
.bottom {
  display: flex;
  justify-content: space-between;
}
.addressAdd {
  text-align: center;
  padding: 5px 20px;
}
</style>



