<template>
  <div>
    <div class="me-header">
      <div class="me-header-inner">
        <div class="me-name">员工姓名：{{me.name}}</div>
        <div class="me-count">积分余额：{{me.balance}}</div>
      </div>
    </div>
    <mt-cell title="我的收货地址" is-link to="/#/address"></mt-cell>
    <mt-cell title="全部礼品" is-link to="/items"></mt-cell>
    <mt-cell title="待收货" is-link to="/MyOrder"></mt-cell>
    <mt-cell title="积分明细" is-link></mt-cell>
    <mt-cell title="积分兑换奖品规则" is-link></mt-cell>
    <div class="items-footer">
      <div class="items-footer-btn" @click="buy">兑换商品</div>
      <div class="items-footer-btn" @click="myInfo">我的信息</div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    me () {
      return this.$store.state.me.me
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$store.dispatch('getMe')
    })
  },
  methods: {
    buy () {
      this.$router.push('/items')
    },
    myInfo () {
      this.$router.push('/me')
    }
  }
}
</script>

<style scoped>
.items-footer {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  height: 50px;
  line-height: 50px;
  display: flex;
  font-size: 20px;
  text-align: center;
}
.items-footer-btn {
  flex: 1;
  border: 1px solid #ececec;
}
.me-header {
  height: 200px;
  background: blue;
  display: flex;
  text-align: center;
}
.me-header-inner {
  margin: auto;
  font-size: 24px;
  background: transparent;
}
.me-count, .me-name {
  margin: 15px 0;
  background: white;
}
</style>




