<template>
  <div>
    <div class="order-wrap" v-for="item in orders" :key="item.id">
      <div class="order">
        <div class="order-img" :style="{backgroundImage: 'url(' + item.image + ')'}">

        </div>
        <div class="order-right">
          <div class="order-name">
            {{item.name}}
          </div>
          <div class="order-price">
            ¥{{item.price}}
          </div>
        </div>
      </div>
      <div class="op">
        <mt-button size="small" type="primary" @click="confirm(item.id)">确认收货</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    orders () {
      let {list} = this.$store.state.items
      let orders = this.$store.state.order.list
      return orders.map((item) => {
        let specifications = list.filter((i) => {
          return item.item_id === i.id
        })[0].specifications
        item.image = JSON.parse(specifications)[0].img[0]
        return item
      })
    }
  },
  beforeRouteEnter (to, from, next) {
    document.title = '我的订单'
    next(vm => {
      vm.$store.dispatch('getOrder')
    })
  },
  methods: {
    confirm (id) {
      this.$store.dispatch('putOrderStatus', id)
    }
  }
}
</script>

<style scoped>
.order-wrap {
  margin: 10px 0;
  padding: 5px 10px;
  border-bottom: 1px solid #ececec;
}
.order {
  position: relative;
}
.order-img {
  width: 100px;
  height: 100px;
  margin: 3px;
}
.order-right {
  position: absolute;
  left: 110px;
  top: 0;
  bottom: 0;
  right: 0;
}
.op {
  text-align: right;
}
.order-img {
  background-size: cover;
  background-position: center;
}
</style>



